<template>
<div>
    <div
        :style='{opacity:percent == 100 ? 0 : 1}'
        class='loading'>
        <div 
            :style='{
                borderRadius:px(20),
                height:px(32),
                top:tpx(852.5,1331),
                width:px(485 * (percent / 100)),
                left:px(132),
            }'
            class='loading-percent'></div>
    </div>
    <div
        class='page-item page-1'        
        :style="{opacity:percent == 100 ? 1 : 0}">
        <div 
            v-if='showQrcode'
            style="position:absolute;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,.8);z-index:1">
            <div
                @click='showQrcode = false'
                :style='{top:tpx(486,1800),right:px(120)}'
                style="position:absolute;width:50px;height:50px;z-index:1"></div>
            <img 
                src='../assets/qrcode.png'
                :style='{top:tpx(478,1800),width:px(524),height:px(750),left:px(118)}'
                style="position:absolute;" />
        </div>
        <div
            class='page-1-btn btn'
            @click="goList"
            :class='isShowBtn ? "page-1-btn-show" : "page-1-btn-hide"'
            :style="{height:px(93),bottom:px(66),width:px(410),left:px(172)}">
        </div>
    </div>
</div>
</template>

<script>
import base from '../base'
import { isWeixin, isZhihu, preload, getRangeRandom } from '../util'

const imgs = {
    "1_1.png":require("assets/1_1.png"),
    "1.gif":require("assets/1.gif"),
    "2_1.png":require("assets/2_1.png"),
    "2_2.png":require("assets/2_2.png"),
    "2_3.png":require("assets/2_3.png"),
    "2_4.png":require("assets/2_4.png"),
    "2_5.png":require("assets/2_5.png"),
    "2_6.png":require("assets/2_6.png"),
    "2_7.png":require("assets/2_7.png"),
    "2_7.jpg":require("assets/2_7.jpg"),
    "2_footer_bg.png":require("assets/2_footer_bg.png"),
    "2.jpg":require("assets/2.jpg"),
    "3.jpg":require("assets/3.jpg"),
    "4_1.jpg":require("assets/4_1.jpg"),
    "4_2.jpg":require("assets/4_2.jpg"),
    "4_3.png":require("assets/4_3.png"),
    "4_4.jpg":require("assets/4_4.jpg"),
    "bg1.png":require("assets/bg1.png"),
    "btn1_1.png":require("assets/btn1_1.png"),
    "btn1.png":require("assets/btn1.png"),
    "btn2.png":require("assets/btn2.png"),
    "event_item_bg.png":require("assets/event_item_bg.png"),
    "loading1.png":require("assets/loading1.png"),
    "qrcode.png":require("assets/qrcode.png"),
}

export default {
    mixins:[base],
    data(){
        return {
            show:true,
            percent:0,
            isShowBtn:false,
            isShowQrcode:!isWeixin() && !isZhihu(),
            showQrcode:false,
            info:{
                loaded:0,
                total:0,
                percent:0,
            }
        }
    },
    watch:{
        "percent"(a){
            if(a == 100){
                this.isShowBtn = true
                setTimeout(() => this.show = false,1000)
            }
        },
        "info.percent"(a){
            if(a == 100){
                this.showPercent()
            }
        }
    },
    async mounted(){
        await preload(Object.values(imgs),this.info)
    },
    activated(){
        this.$main.za(10189)
    },
    methods:{
        goList(){
            // if(this.isShowQrcode){
            //     this.showQrcode = true
            //     return;
            // }
            this.$router.push('list')
        },
        showPercent(){
            const fn = (num) => {
                num = num || 0
                if(num == 4){
                    this.percent = 100
                    return;
                }
                this.percent = getRangeRandom(num * 20,(num + 1) * 20)
                setTimeout(() =>{
                    fn(num + 1)
                },500)
            }
            fn()
        }
    }

}
</script>

<style lang='scss'>
.loading{
    opacity: 1;
    transition: all .3s;
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    background: url(../assets/loading-bg.jpg);
    background-size: 100%;
    background-position: center;
    &-percent{
        background:#FDD679;
        position: absolute;
        transition: all .3s;
    }
}
</style>