import Vue from 'vue'
import VueRouter from 'vue-router'
import Loading from '../components/loading.vue'
import List from '../components/list.vue'
import Capture from '../components/capture.vue'
import { isWeixin, getQueryInfo } from '../util'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'loading',
    component: Loading
}, {
    path: '/list',
    name: 'list',
    component: List,
}, {
    path: '/capture',
    name: 'capture',
    component: Capture
}]

const router = new VueRouter({
    mode: "hash", 
    routes
})

router.beforeEach((to, from, next) => {
    const base = `${location.origin}${location.pathname}`
    const weixin = `https://www.zhihu.com/oauth/redirect/social_info/billboardseventy?from=${base}?t=${Date.now()}`
    //----------------------------
    // 首页必须是 loadding 页
    //----------------------------
    if(!from.name){
        if(to.name != 'loading'){
            location = base
            return;
        }
    }
    //----------------------------
    // 微信端获取个人信息
    //----------------------------
    if(isWeixin()){
        const me = getQueryInfo()
        if(!me || !me.nickname || !me.t){
            location = weixin
            return;
        }
        //验证昵称获取的时间
        if(to.name == "loading" && Math.abs(Date.now() - parseInt(me.t)) > 5000){
            location = weixin
            return;
        }
    }
    //----------------------------
    next()
})

export default router