import { getMain } from './util'
import _pad from "lodash.padstart"
import _range from "lodash.range"

export default {
    data(){
        return {
            eventItem:{
                size:"",
                radius:"",
                left:"",
                top:"",
            },
            scrollHeight:0,
            scrollHeightValue:0,
        }
    },
    created(){        
        this.eventItem.size = this.px(160)
        this.eventItem.radius = this.px(10)
        this.eventItem.left = this.px(19)
        this.eventItem.top = this.px(19)

    },
    computed:{
        $main(){
            return getMain(this)
        },
        innerHeight(){
            return window.innerHeight
        }
    },
    methods:{
        _pad,
        _range,
        /**
         * 转成实际像素
         */
        px(v){
            return v * this.$main.scale + "px"
        },
        /**
         * y 轴实际像素
         */
        tpx(v,h){
            const top = (window.innerHeight - h * this.$main.scale) / 2
            return top + v * this.$main.scale + "px"
        },
        updateScrollHeight(height){
            this.scrollHeightValue = this.innerHeight - parseFloat(height)
            this.scrollHeight = this.scrollHeightValue + "px"
        }
    }
}