

export function getMain(component){
    if(component){
        if(component.isMain){
            return component
        }else{
            return getMain(component.$parent)
        }
    }
}

export function getRangeRandom(a,b){
    return Math.floor(Math.random() * (b - a + 1) + a)
} 

export function isWeixin(){    
    const ua = navigator.userAgent.toLowerCase();
    return (/micromessenger/i.test(ua)) ? true : false ;
}

export function isZhihu(){
    const ua = navigator.userAgent.toLowerCase();    
    return (/Zhihu/i.test(ua)) ? true : false ;
}

export function getBase64Image(url) {
    return new Promise ((resolve,reject) => {
        let image = new Image();
        image.onload = function() {
            let canvas = document.createElement('canvas');
            canvas.width = this.naturalWidth;
            canvas.height = this.naturalHeight;
            // 将图片插入画布并开始绘制
            canvas.getContext('2d').drawImage(image, 0, 0);
            // result
            let result = canvas.toDataURL('image/png')
            resolve(result);
        };
        // CORS 策略，会存在跨域问题https://stackoverflow.com/questions/20424279/canvas-todataurl-securityerror
        image.setAttribute('crossOrigin', 'anonymous');
        image.src = url;
        // 图片加载失败的错误处理
        image.onerror = (e) => {
            reject(e);
        };
    })
}

export function getQueryInfo(){
    return (location.search.split("?")[1] || "").split("&").reduce((res,item) => {
        const [ key, value ] = item.split("=")
        res = res || {}
        res[key] = decodeURIComponent(value)
        return res
    },{})
}

export function preload(list,info){
    const total = list.length;
    info.total = total
    const imgs = []
    return new Promise((resolve) => {
        let count = 0
        let index = 0
        list.forEach((src) => {
            index++;
            imgs[index] = new Image();
            imgs[index].onerror = (err) => {
                console.log(err);
            };
            imgs[index].onload = () => {
                count++;
                if(count == total){
                    resolve(true)
                }
                info.loaded = count
                info.percent = Math.ceil(count / total * 100)
            };
            imgs[index].src = src
        })
    })

}