<template>
<div 
    class='page'>
    <div 
        @click.stop='show.detail = false'
        v-if='show.detail'
        style="width:100%;height:100%;position:absolute;top:0;left:0;background:rgba(0, 0, 0, 0.6);z-index:9;"></div>
    <swiper
        :style="{height:px(750),top:tpx(476,1800)}"
        ref='details'
        v-if='show.detail'
        class="swiper events-detail"
        @slideChangeTransitionEnd='detailsEnd'
        :options="swiperOption">
        <template 
            v-for='(event,index) in eventList'>
            <swiper-slide
                :style="{width:px(522),height:px(750),borderRadius:px(10),background:types[event.event_type].color}"
                :key='`detail-${index}`'>
                <div>
                    <div 
                        @click.stop="show.detail = false"
                        :style="{width:px(68),height:px(68)}"
                        style="position:absolute;right:0;top:0;"></div>
                    <div
                        :style="{left:px(66),height:px(54),lineHeight:px(54),width:px(148),top:px(54)}"
                        style="position:absolute;right:0;top:0;text-align:center;color:#FFF;"><i>{{event.event_month}}月{{event.event_day}}日</i></div>
                    <!--加入备忘-->
                    <div 
                        v-show='!selectIndex[index]'
                        @click.stop="selectEvent(index,event,1)"
                        :style="{bottom:px(45),height:px(94)}"
                        style="position:absolute;width:100%;"></div>
                    <div
                        @click.stop="selectEvent(index,event,1)"
                        class='btn2'
                        :style="{bottom:px(45),height:px(94)}"
                        style="position:absolute;width:100%;"
                        v-show='selectIndex[index]'></div>
                    <!---->
                    <div
                        :style="{top:px(130),paddingLeft:px(64),paddingRight:px(64)}"
                        style="color:#FFF;text-align:left;position:absolute;font-size:15px;">
                        <div>{{event.event_name}}</div>
                        <div
                            style="font-size:14px;"
                            :style='{marginTop:px(50)}'>{{event.related_answer_essence}}</div>
                    </div>
                    <div
                        :style="{bottom:px(180),paddingLeft:px(64),paddingRight:px(64)}"
                        style="color:rgba(255,255,255,.8);text-align:left;position:absolute;font-size:12px;">{{`来自 ${event.author_token} 的回答「${event.related_question_title}」`}}</div>
                </div>
            </swiper-slide>
        </template>
    </swiper>
    <div
        class='page-item page-2'
        :style="{opacity:index == 2 ? 1 : 0,...zIndex(2)}">
        <div class='page-2-top'>
            <img src='../assets/2_2.png' style="width:100%;" />
        </div>
        <div
            :style='{top:px(131)}'
            style="position:absolute;width:100%;text-align:center;color:#2570EF">
            <span>已备忘</span>
            <span :style="{marginLeft:px(20)}">{{selectEvents.length}} / {{total}}</span>
        </div>
        <div
            :style="{
                left:px(11),
                top:px(218),
                width:px(94),
                height:px(830)}"
            class='months'>
            <div
                @click="selectMonth(index)"
                :style="{height:px(56),lineHeight:px(60),marginTop:px(13.5),borderRadius:px(12)}"
                :id='"months"+index'
                :class='[selectMonthIndex == index && "months-active","months-"+index]'
                :key='"months" + index'
                v-for='(item,index) in _range(12)'>
                {{index + 1}} 月
            </div>
        </div>
        <div 
            ref='events'
            :style="{
                top:px(218),
                height:scrollHeight,
                width:px(545),
                left:px(120 - 19)}" 
            class='events'>
            <template
                v-for='(item,index) in eventList'>
                <EventItem
                    :key='`events-${index}`'
                    :class='"events-"+index'
                    :selected='selectKeys.indexOf(index) > -1'
                    :item='item'
                    @select="selectEvent(index,item)"
                    @detail='showDetail(index)'
                    :isShowCheck="true" />
            </template>
        </div>
        <div 
            :style="{height:px(308)}"
            class='page-2-footer'>
            <div
                :style="{bottom:px(260),height:px(20),width:px(572)}"
                class='page-2-footer-bg1'></div>
            <div
                style="animation:next 1s linear infinite;"
                @click="selectMonth(selectMonthIndex + 1)"
                :style="{height:px(32),bottom:px(206),width:px(60)}"
                class='page-2-btn1'>
            </div>
            <div
                class='page-2-btn'
                @click="goCapture"
                :style="{height:px(93),bottom:px(90),width:px(410)}"
                :class="selectKeys.length && 'page-2-btn-active'"></div>
        </div>

    </div>
</div>
</template>

<script>
import base from '../base'
import axios from 'axios'
import _sortby from 'lodash.sortby'
// import _clone from 'lodash.clonedeep'
import _debounce from 'lodash.debounce'
import tween from '@tweenjs/tween.js'
import { isWeixin, isZhihu } from '../util'
import EventItem from './event.vue'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/swiper-bundle.css'
import "../utils/zsencrypt"
import types from './types'

export default {
    mixins:[base],
    components:{
        EventItem,
        Swiper,
        SwiperSlide,
    },
    data(){
        return {
            index:2,
            events:[],
            eventList:[],
            total:0,
            selectKeys:[],
            selectEvents:[],
            selectIndex:{},
            selectMonthIndex:0,
            isShowQrcode:!isWeixin() && !isZhihu(),
            showQrcode:false,
            isLoadingCapture:false,
            swiperOption: {
                slidesPerView: 'auto',
                centeredSlides: true,
                spaceBetween: 30,
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true
                },
                virtual:true,
            },
            isViewEvents:false,
            show:{
                1:false,
                detail:false,
            },
            types,
            detailIndex:0,
            selectItem:null,
        }
    },
    watch:{
        index(a){
            if(a == 1){
                setTimeout(() => this.show[1] = true,200)
            }
            if(a == 3){
                this.isLoadingCapture = true
            }
        },
        'show.detail'(a){
            if(!a && this.selectItem){
                this.selectMonth(this.selectItem.event_month - 1)
                this.selectItem = null
            }
        }
    },
    computed:{
        eventItemHeight(){
            return parseFloat(this.eventItem.size) + parseFloat(this.eventItem.top)
        },
    },
    created(){
        this.updateScrollHeight(parseFloat(this.px(268)) + parseFloat(this.px(218)))
    },
    async mounted(){
        let events = await axios.get("https://www.zhihu.com/events/2020")
        events = (events && events.data && events.data.data || []).map(item => {
            item.event_day = parseInt(item.event_day)
            item.event_month = parseInt(item.event_month)
            return item
        })
        // events = events.concat(_clone(events)).concat(_clone(events)).concat(_clone(events))
        this.total = events.length
        events = _sortby(events,['event_month','event_day'])
        events = events.map((item,index) => {
            item.index = index
            return item
        })
        this.eventList = events
        this.events = events.reduce((res,item) => {
            res = res || {}
            res[item.event_month] = res[item.event_month] || {}
            res[item.event_month].month = item.event_month
            res[item.event_month].events = res[item.event_month].events || []
            res[item.event_month].events.push(item)
            return res
        },{})
        this.events = Object.values(this.events)
        this.$refs.events.addEventListener("scroll",_debounce(this.eventScroll,100))
    },
    activated(){
        this.$main.za(10189)
    },
    methods:{
        goCapture(){
            if(!this.selectKeys.length){
                return;
            }
            this.$main.za(10192)
            this.$main.selectEvents = this.selectEvents
            this.$router.push("capture")
        },
        zIndex(a){
            return {
                zIndex:this.index == a ? 2 : 1
            }
        },
        selectEvent(key,item,isDetail){
            if(isDetail){
                this.$main.za(10195)
            }else{
                this.$main.za(10190)
            }
            const index = this.selectKeys.indexOf(key)
            if(index > -1){
                this.selectIndex[key] = false
                this.selectKeys.splice(index,1)
                this.selectEvents.splice(index,1)
            }else{
                this.selectIndex[key] = true
                this.selectKeys.push(key)
                this.selectEvents.push(item)
            }
        },
        eventScroll(e){
            if(this.lockScroll){
                return;
            }
            const { scrollTop } = e.target
            const row = parseInt(scrollTop / this.eventItemHeight)
            const start = row * 3
            const month = this.eventList.slice(start,start + 3).pop()
            this.selectMonthIndex = month.event_month - 1
        },
        selectMonth(index){
            if(index > 11){
                index = 0
            }
            this.selectMonthIndex = index
            const event = this.eventList.filter(item => item.event_month >= index + 1)[0]
            const eIndex = event && event.index
            if(!(eIndex >= 0)){
                return;
            }
            const row = parseInt(eIndex / 3)
            this.scrollTo(row * this.eventItemHeight)
            this.lockScroll = true
            setTimeout(() => this.lockScroll = false,500)
        },
        scrollTo(scrollTop){
            const _s = this
            const start = this.$refs.events.scrollTop, end = scrollTop
            function animate () {
                if (tween.update()) {
                    requestAnimationFrame(animate)
                }
            }
            const data = { scrollTop: start }
            new tween
                .Tween(data)
                .to({ scrollTop: end }, 300)
                .onUpdate(function () {
                    _s.$refs.events.scrollTop = data.scrollTop
                })
                .start()
            animate()
        },
        showDetail(key){
            this.show.detail = true
            if(this.$refs && this.$refs.details){
                this.$main.za(10191)
                this.$refs.details.$swiper.slideTo(key, 0)
                this.detailIndex = this.$refs.details.$swiper.activeIndex
                if(this.detailIndex == 0){
                    this.$main.za(10194)
                }
                return;
            }
            setTimeout(() => this.showDetail(key),50)
        },
        detailsEnd(e){
            this.detailIndex = e.activeIndex
            this.selectItem = this.eventList[e.activeIndex]
            this.$main.za(10194)
        }
    }
}
</script>

<style lang='scss'>

@keyframes next
{
    0% {transform: translateY(0px);}
    25% {transform: translateY(-3px);}
    50%{transform: translateY(0);}
    75% {transform: translateY(3px);}
    100% {transform: translateY(0);}
}
</style>