<template>
  <div id="app">
    <keep-alive>
        <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
import za from 'za-js-sdk'
import { isWeixin, isZhihu } from './util'
import fetchData from "./utils/bundle.min"
import axios from 'axios'

export default {
    name: 'App',
    data(){
        return {
            isMain:true,
            scale:window.innerWidth / 750,
            page:1,
            height:window.innerHeight,
            width:window.innerWidth,
            isDev:process.env.NODE_ENV === "development",
            me:{
                name:"",
                avatar_url:""
            },
            selectEvents:[],
        }
    },
    async created(){
        za.config({ enableLog:this.isDev})
        //----------------------------
        // 微信端获取个人信息
        //----------------------------
        if(isWeixin()){
            const me = (location.search.split("?")[1] || "").split("&").reduce((res,item) => {
                const [ key, value ] = item.split("=")
                res = res || {}
                res[key] = decodeURIComponent(value)
                return res
            },{})
            this.me.name = me.nickname
            this.me.avatar_url = me.avatar
        }
        //----------------------------
        // 知乎端获取个人信息
        //----------------------------
        if(isZhihu()){
            const meUrl = 'https://www.zhihu.com/api/v4/me'
            alert("开始获取知乎端获取个人信息")
            try{
                const me = await fetchData(meUrl);
                alert(JSON.stringify(me))
                if(me && me.data){
                    this.me = me.data
                }
                alert(JSON.stringify(this.me))
            }catch(e){
                alert(e)
                console.warn(e)
            }
            axios.get(meUrl).then(res => {
                alert("22222" + JSON.stringify(res))
            }).catch(err => {
                alert("err:" + JSON.stringify(err))
            })
        }        
    },
    methods:{
        za(id){
            let isEvent, isCardShow, isPageShow, type, text
            switch(id){
                case 10197:
                    isEvent = true
                    type = "Button"
                    text = "查看更多"
                    break;
                case 10196:
                    isEvent = true
                    type = "Button"
                    text = "保存"
                    break;
                case 10195:
                    isEvent = true
                    type = "Button"
                    text = "事件加⼊备忘"
                    break;
                case 10194:
                    isCardShow = true
                    type = "Popup"
                    text = "事件详情"
                    break;
                case 10193:
                    isEvent = true
                    type = "Button"
                    text = "分享"
                    break;
                case 10192:
                    isEvent = true
                    type = "Button"
                    text = "⽣成备忘录"
                    break;
                case 10191:
                    isEvent = true
                    type = "Button"
                    text = "事件展开"
                    break;
                case 10190:
                    isEvent = true
                    type = "Button"
                    text = "事件选择"
                    break;
                case 10189:
                    isPageShow = true
                    type = "Page"
                    text = "⻚⾯曝光"
                    break;
                default:
                    return;
            }
            let fn = isEvent && za.trackClick;
            fn = fn || isCardShow && za.trackShow;
            fn = fn || isPageShow && za.trackPageShow
            fn({
                type,
                block:id+"",
                text
            })
            if(this.isDev){
                console.log(id,text)
            }           
        }
    }
}
</script>

