<template>
<div
    @click='$emit("detail")'
    class='events-item'
    :style="{
        borderRadius:eventItem.radius,
        width:itemSize || eventItem.size,
        marginLeft:eventItem.left,
        marginTop:eventItem.top,
        height:itemSize || eventItem.size}" >
    <div
        @click.stop="$emit('select')"
        :style="{background:types[item.event_type].color}"
        style="display: flex;
            align-items: center;
            justify-content:space-between;"
        class='events-item-time'>
        <div
            style="color:#FFF;"
            :style="{padding:px(5)}">{{dayjs(`2020-${_pad(item.event_month,2,"0")}-01`).format("MMM")}}.{{_pad(item.event_day,2,0)}}</div>
        <div v-if='isShowCheck'>
            <div 
                :style="{width:px(32),height:px(32),marginRight:px(10)}"
                class="events-item-time-check">
                <div 
                    v-if='selected'
                    :style="{
                        top:px(6),
                        left:px(3),
                        width:px(20),
                        height:px(10),
                        borderColor:'#D481AE'}"></div>
            </div>
        </div>
    </div>
    <div
        style="background:#FFF;">
        <div
            class='events-item-bg'>
            <div        
                class='events-item-name'
                :style="{padding:px(5),height:px(112),borderBottomRightRadius:eventItem.radius,borderBottomLeftRadius:eventItem.radius}">
                <div>{{item.event_name}}</div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import base from '../base'
import dayjs from 'dayjs'
import types from './types'

export default {
    mixins:[base],
    props:{
        item:Object,
        selected:Boolean,
        isShowCheck:Boolean,
        itemSize:String,
    },
    data(){
        return {
            types,
        }
    },
    methods:{
        dayjs,
    }

}
</script>

