<template>
<div
    class='page-item page-3'>
    <div
        v-show='!capture'
        :style="{height:innerHeight+'px'}"
        style="position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,.8);z-index:100;transition:all 1s;">
        <div style="position:absolute;top:50%;left:50;color:#FFF;font-size:14px;text-align:center;width:100%;margin-top:-20px;">加载中..</div>
    </div>
    <div
        ref='capture' 
        class='capture'>
        <template>
            <img src='../assets/4_1.jpg' style="width:100%;display:block;" />
            <img
                crossorigin="anonymous"
                :style='{top:px(65),left:px(25),width:px(133),height:px(133)}'
                style="position:absolute;border-radius:50%;overflow: hidden;z-index:1"
                :src='headimgData || $main.me.avatar_url' />
            <div
                :style="{top:px(74),left:px(164),width:px(240),height:px(60),lineHeight:px(60)}"
                style="position:absolute;
                    overflow: hidden;
                    background:#4589EA;
                    color:#FFF;
                    text-align:center;">{{$main.me.name}}</div>
            <div
                :style="{height:listHeight+'px'}"
                class='capture-list'></div>
            <div
                ref='list'
                class='capture-list1'>
                <div                
                    :style='{
                        minHeight:innerHeight - parseFloat(px(542)) +"px",
                        marginTop:px(-258),
                        width:px(600),
                        paddingLeft:px(74)}'>
                    <div
                        style="text-align:center;
                        color:#FFF;">备忘了 <span style="font-size:22px;color:#EBD087;font-weight:bold;">{{$main.selectEvents.length}}</span> 件大事</div>
                    <div
                        :style="{marginTop:px(40)}"
                        style="display:inline-block;
                            text-align:left;display:flex;flex-wrap: wrap;">
                        <EventItem
                            style="background:inherit!important"
                            :key='`events-${index1}`'
                            v-for='(event,index1) in $main.selectEvents'
                            :item='event'
                            :eventItem='eventItem'
                            :itemSize='parseFloat(eventItem.size) + parseFloat(px(11)) + "px"'
                            class='events-item' />
                    </div>
                </div>
            </div>
            <img                
                v-if='!capture'
                :style="{marginTop:px(-220)}"
                src='../assets/4_3.png'
                style="position: relative;width:100%;z-index:2;display:block;" />
        </template>
    </div>
    <template v-if='capture'>
        <!-- <div
            class='btn'
            :style="{height:px(278)}"></div> -->
        <div
            style="z-index:1"
            :style="{height:px(278)}"
            class='capture-save btn'>
            <!--查看更多按钮-->
            <div
                @click="more"
                class='btn'
                :style="{bottom:px(53),width:px(214),height:px(90),right:px(118)}"
                style="position:absolute;cursor:pointer;"></div>
        </div>
        <img 
            :style="{height:px(200)}"
            @touchstart="$main.za(10196)"
            style="display:block;position:fixed;bottom:0;left:0;width:50%;height:100%;z-index:2;opacity: .5;"
            :src='capture' />
    </template>

</div>
</template>

<script>
import html2canvas from 'html2canvas'
import base from '../base'
import EventItem from './event.vue'
import { getBase64Image } from '../util'

export default {
    mixins:[base],
    components:{
        EventItem,
    },
    data(){
        return {
            capture:"",
            headimgData:"",
            listHeight:0,
        }
    },
    created(){
        this.updateScrollHeight(parseFloat(this.px(512)))
    },
    activated(){
        this.$main.za(10189)
        this.capture = ""
        document.body.style.overflow = 'auto';
        this.$nextTick(() => {
            this.listHeight = this.$refs.list.clientHeight
            this.getHeadimgData()
        })        
    },
    methods:{
        getListHeight(){
            const itemHeight = parseFloat(this.eventItem.size) + parseFloat(this.px(11))
            return Math.ceil(this.$main.selectEvents.length / 3) * itemHeight;
        },
        more(){
            this.$main.za(10197)
            setTimeout(() => {
                location = "https://www.zhihu.com/search?type=content&q=2020"
            },200)
        },
        getHeadimgData(){
            const fn = () => {
                setTimeout(() => {
                    html2canvas(this.$refs.capture).then(canvas => {
                        this.capture = canvas.toDataURL("image/jpeg",1)
                    });
                },2000)
            }
            if(!this.headimgData && this.$main.me.avatar_url){
                alert(`url:${this.$main.me.avatar_url}`)
                getBase64Image(this.$main.me.avatar_url+"?t="+ Date.now()).then(res => {
                    alert("11111:"+res)
                    this.headimgData = res
                }).catch(e => {
                    alert(JSON.stringify(e))
                }).finally(() => {
                    fn()
                })

                getBase64Image(this.$main.me.avatar_url.replace("https","http") + "?t="+Date.now()).then(res => {
                    alert("2222:"+res)
                    this.headimgData = res
                }).catch(e => {
                    alert(JSON.stringify(e))
                }).finally(() => {
                    fn()
                })



                return
            }
            fn()
        },
    }
}
</script>

<style lang='scss'>
.capture{
    &-list{
        z-index: -1;
        position:absolute;
        background:url(../assets/4_2.jpg);
        background-size: 100%;
        width:100%;
        margin-top:-2px;
    }
    &-list1{
        // z-index: 1;
        // background:url(../assets/4_2.jpg);
        // background-size: 100%;
        // width:100%;
        // margin-top:-2px;
    }
    &-save{
        width:100%;
        position:fixed;
        bottom:0;
        left:0;
        background:url(../assets/4_4.jpg);
        background-size: 100%;
    }
}
</style>